:root,
body {
  height: 100%;
  height: 100vh;
  width: 100%;
  width: 100vw;
}

#root {
  height: 100%;
  overscroll-behavior-y: none;
}

.navbar.has-shadow {
  box-shadow: 0 1px 2px 0 #1a1a1a55;
}

.card {
  border-radius: 0.5em;
}

.has-background {
  background: linear-gradient(
      180deg,
      #dfd8d947 0,
      #f5f5f5ee 25%,
      #fff 70%,
      #fafafade 100%
    ),
    url(deal_bg.jpg);
}

.section.is-marginless-horizontal {
  padding-left: 0;
  padding-right: 0;
}

.has-transition-1,
.has-transition-1 * {
  transition: all 1s ease;
}

.is-height-0 {
  animation: compress 1s ease;
}

@keyframes compress {
  100% {
    height: 0;
    display: none;
  }
}
